import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Chip } from '@mui/material';
import ProgressBar from 'react-bootstrap/ProgressBar';
import Footer from './Footer';
import db from 'libs/db';
import CurrencyInput from 'react-currency-input-field';
export default function InvestToday(props) {

	const [investToday, setInvestToday] = React.useState();
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const STATE = props.location.state
	
	React.useEffect(async ()=>{
		await db.get(db.model.createGoal).then(p=>{
			if(p && p.investToday !== undefined){
				setInvestToday(p.investToday)
			} else {
				setInvestToday('')
			}
		})
	},[])

	const checkValidation = () => {
		if (investToday!=='' && investToday >= 0) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [investToday])

	if (STATE === undefined) {
		props.history.push('/Goals')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header={STATE.header}
				navigation={props.history}
			/>
			<ProgressBar now={60} className="progress-bar-custom" />
			<div className='dataContainer'>
				<div className="bodyTextDark" style={{ marginTop: "6%" }}>{STATE.investTodayScreen.question}</div>
				{/* <input
					type="tel"
					id="investToday"
					className='normalInput rupee'
					placeholder={STATE.investTodayScreen.placeholder}
					value={investToday}
					onChange={(num) => {
						const a = Number(num.target.value.replace(/\D/g, ''))
						setInvestToday(a);
					}}
				/> */}
				<CurrencyInput
					className='normalInput rupee'
					id="investToday"
					name="investToday"
					placeholder={STATE.investTodayScreen.placeholder}
					value={investToday}
					allowDecimals={false}
					allowNegativeValue={false}
					disableAbbreviations={false}
					prefix=' '
					autoFocus
					onValueChange={(value) => setInvestToday(Number(value) || 0)}
					intlConfig={{ locale: 'en-IN', currency: 'INR' }}
				/>
				<div className='hiddden-scrollbar'>
					{STATE.investTodayScreen.chips.map((item) => {
						return (
							<Chip
								className={investToday === item.key ? "normalChip selected" : "normalChip"}
								key={item.key}
								label={item.name}
								onClick={() => {
									setInvestToday(item.key)
								}}
							/>
						)
					})}
				</div>
			</div>
			{/* <div className='footer'>
				<button
					className='solidButton'
					style={{ opacity: nextDisabled ? 0.4 : 1 }}
					onClick={() => {
						if(!nextDisabled){
							props.history.push({ pathname: '/Goals/monthly-increase', state: { ...STATE, investToday }})
						}
					}}
				>Confirm</button>
			</div> */}
			<Footer
				navigation={props.history}
				next='/Goals/monthly-increase'
				state={{ ...STATE, investToday }}
				saveData = {{ investToday }} 
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
